import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, IconButton, Toolbar, Collapse } from '@material-ui/core';
import SortIcon from '@material-ui/icons/Sort';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Link as Scroll } from 'react-scroll';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        fontFamily: 'Nunito',
    },
    appbar: {
        background: 'none',
    },
    appbarWrapper: {
        width: '80%',
        margin: '0 auto',
    },
    appbarTitle: {
        flexGrow: '1',
    },
    icon: {
        color: '#fff',
        fontSize: '2rem',
    },
    colorText: {
        color: '#5AFF3D',
    },
    container: {
        textAlign: 'center',
    },
    title: {
        color: '#fff',
        fontSize: '4.5rem',
    },
    goDown: {
        color: '#5AFF3D',
        fontSize: '4rem',
    },
    }));



function Header(props) {
    const classes = useStyles();
    const [checked, setChecked] = useState(false);
    useEffect(() => {
        setChecked(true);
    }, []);
    return (
        <div className={classes.root} id="header">
        <AppBar className={classes.appbar} elevation={0}>
            <Toolbar className={classes.appbarWrapper}>
            <h1 className={classes.appbarTitle}>
                Re<span className={classes.colorText}>Cyclup.</span>
            </h1>
            {
            props.user ? (
                        <>    <IconButton>
                        <Button onClick={props.onHandleProfile}>Profile</Button> </IconButton>
                        <Button variant="contained" color="secondary" onClick={props.onLogOut}>Logout</Button>
                        </>
                        ) : (
                        <>
                <Link to={'/signin'}><Button variant="contained" color="secondary" >SignIn</Button></Link>
                <Link to={'/signup'}><Button  variant="contained" color="secondary">SignUp</Button></Link>
                    </>
                )}
            
            </Toolbar>
        </AppBar>
        <Collapse
            in={checked}
            {...(checked ? { timeout: 1000 } : {})}
            collapsedHeight={50}
        >
            <div className={classes.container}>
            <h1 className={classes.title}>
                Welcome to <br />
                Re<span className={classes.colorText}>Cyclup.</span>
            </h1>
            <Scroll to="place-to-visit" smooth={true}>
            <IconButton>
            <ExpandMoreIcon className={classes.goDown} />
            </IconButton>
        </Scroll>
            </div>
        </Collapse>
        </div>
    );
    }


    export default Header;